  justify-content: space-between;
  /* padding-top: 50px; */
  /* text-align: center; */
}

.navbar h1 {
  /* padding-bottom: 20px; */
  text-transform: capitalize;
}

/** button group styles **/
.btn-group {
  border-radius: 1rem;
  text-transform: capitalize;
}
.btn-group__item {
  border: none;
  /* min-width: 6rem; */
  padding:  1rem  2.2rem 1rem 2.2rem;
  background: #1F2833;
  color: white;
  cursor: pointer;
  margin: 0;
  font-size: 17px;
  box-shadow: inset 0px 0px 0px -15px rgb(255, 255, 255);
  transition: all 300ms ease-out;
  text-transform: capitalize;
}
.btn-group__item:last-child {
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.btn-group__item:first-child {
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.btn-group__item:hover,
.btn-group__item:focus {
  color: #66FcF1;
      box-shadow: inset 0px -20px 0px -15px rgb(255, 255, 255);
}
.btn-group__item:focus {
  outline: none;
}
.btn-group__item:after {
  margin-left: 0.5rem;
  content: "✔️";
  display: inline-block;
  color: rgb(156, 233, 248);
  position: absolute;
  transform: translatey(10px);
  opacity: 0;
  transition: all 200ms ease-out;
}
.btn-group__item--active:after {
  opacity: 1;
  transform: translatey(-2px);
}



/* ................... */
.search-box{
  width: fit-content;
  height: fit-content;
  position: relative;
}
.input-search{
  height: 50px;
  width: 50px;
border: 1px solid transparent;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all .5s ease-in-out;
  background-color: #fff5c3;
  padding-right: 40px;
  color:#000000;
}
.input-search::placeholder{
  color:rgba(0, 0, 0, 0.5);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
}
.btn-search{
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color:#ffffff ;
  background-color:transparent;
  pointer-events: painted;  
}
.btn-search:focus ~ .input-search{
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom:1px solid rgba(255,255,255,.5);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}
.input-search:focus{
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom:1px solid rgba(255,255,255,.5);
  transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}


@media screen and (min-width:340px) and (max-width: 800px) {
  
  .navbar{
    display: none !important;
  
    width: 80%;
  }

  .card{
    width: 90%;
  }
  


}

/* ......................................... */



.card .back {
  position: absolute;
  transform: rotateY(0deg);
  backface-visibility: hidden;
  transition: transform 0.4s linear;
}
.card .front {
  position: absolute;
  transform: rotateY(180deg);
  backface-visibility: hidden;
  transition: transform 0.4s linear;
}
.card:hover .back {
  transform: rotateY(-180deg);
  cursor: pointer;
}
.card:hover .front {
  transform: rotateY(0deg);
  cursor: pointer;
}

